import {
  Modal,
  ModalOverlay,
  ModalContent,
  ModalHeader,
  ModalFooter,
  ModalBody,
  ModalCloseButton,
  Button,
  FormControl,
  FormLabel,
  Input,
  Select,
  VStack,
  useToast,
  useColorMode,
} from '@chakra-ui/react';
import { useState, useEffect } from 'react';
import { apiService, Email } from '../services/api';

interface TransactionFormProps {
  isOpen: boolean;
  onClose: () => void;
  transaction?: Email;
  onSuccess: () => void;
}

const CATEGORIES = [
  'n ung',
  'Di chuyn',
  'Mua sm',
  'Ha n & Tin ch',
  'Gii tr',
  'Sc khe',
  'Gio dc',
  'Khc',
];

// Convert Vietnamese text to uppercase without diacritics
const convertToUpperCase = (text: string): string => {
  const vietnameseMap: { [key: string]: string } = {
    '': 'A', '': 'A', '': 'A', '': 'A', '': 'A',
    '': 'A', '': 'A', '': 'A', '': 'A', '': 'A', '': 'A',
    '': 'A', '': 'A', '': 'A', '': 'A', '': 'A', '': 'A',
    '': 'E', '': 'E', '': 'E', '': 'E', '': 'E',
    '': 'E', '': 'E', '': 'E', '': 'E', '': 'E', '': 'E',
    '': 'I', '': 'I', '': 'I', '': 'I', '': 'I',
    '': 'O', '': 'O', '': 'O', '': 'O', '': 'O',
    '': 'O', '': 'O', '': 'O', '': 'O', '': 'O', '': 'O',
    '': 'O', '': 'O', '': 'O', '': 'O', '': 'O', '': 'O',
    '': 'U', '': 'U', '': 'U', '': 'U', '': 'U',
    '': 'U', '': 'U', '': 'U', '': 'U', '': 'U', '': 'U',
    '': 'Y', '': 'Y', '': 'Y', '': 'Y', '': 'Y',
    '': 'D',
  };

  return text
    .toLowerCase()
    .split('')
    .map(char => vietnameseMap[char] || char.toUpperCase())
    .join('')
    .replace(/\s+/g, '_');
};

// Format VND currency
const formatVND = (amount: number) => {
  return new Intl.NumberFormat('vi-VN', {
    style: 'currency',
    currency: 'VND',
    minimumFractionDigits: 0,
    maximumFractionDigits: 0,
  }).format(amount);
};

export function TransactionForm({ isOpen, onClose, transaction, onSuccess }: TransactionFormProps) {
  const toast = useToast();
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [formData, setFormData] = useState({
    emailId: '',
    expense: '',
    month: new Date().getMonth() + 1,
    price: '',
    category: 'Khc',
    note: '',
  });

  useEffect(() => {
    if (transaction) {
      setFormData({
        emailId: transaction.emailId,
        expense: transaction.expense,
        month: transaction.month,
        price: transaction.price.toString(),
        category: transaction.category || 'Khc',
        note: transaction.note || '',
      });
    } else {
      setFormData({
        emailId: '',
        expense: '',
        month: new Date().getMonth() + 1,
        price: '',
        category: 'Khc',
        note: '',
      });
    }
  }, [transaction]);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setIsSubmitting(true);

    try {
      const convertedCategory = convertToUpperCase(formData.category);
      if (transaction) {
        await apiService.updateEmail(transaction.id, {
          expense: formData.expense,
          month: formData.month,
          price: parseFloat(formData.price),
          category: convertedCategory,
          note: formData.note,
        });
        toast({
          title: 'Cp nht thnh cng',
          status: 'success',
          duration: 3000,
          isClosable: true,
        });
      } else {
        await apiService.createEmail({
          emailId: formData.emailId,
          expense: formData.expense,
          month: formData.month,
          price: parseFloat(formData.price),
          category: convertedCategory,
          note: formData.note,
        });
        toast({
          title: 'To mi thnh cng',
          status: 'success',
          duration: 3000,
          isClosable: true,
        });
      }
      onSuccess();
      onClose();
    } catch (error) {
      toast({
        title: 'Li',
        description: error instanceof Error ? error.message : ' xy ra li',
        status: 'error',
        duration: 3000,
        isClosable: true,
      });
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <Modal isOpen={isOpen} onClose={onClose}>
      <ModalOverlay />
      <ModalContent>
        <form onSubmit={handleSubmit}>
          <ModalHeader>
            {transaction ? 'Chnh Sa Giao Dch' : 'To Giao Dch Mi'}
          </ModalHeader>
          <ModalCloseButton />
          <ModalBody>
            <VStack spacing={4}>
              {!transaction && (
                <FormControl isRequired>
                  <FormLabel>ID Email</FormLabel>
                  <Input
                    value={formData.emailId}
                    onChange={(e) => setFormData({ ...formData, emailId: e.target.value })}
                    placeholder="Nhp ID email"
                  />
                </FormControl>
              )}
              <FormControl isRequired>
                <FormLabel>M T Chi Tiu</FormLabel>
                <Input
                  value={formData.expense}
                  onChange={(e) => setFormData({ ...formData, expense: e.target.value })}
                  placeholder="Nhp m t chi tiu"
                />
              </FormControl>
              <FormControl isRequired>
                <FormLabel>Thng</FormLabel>
                <Select
                  value={formData.month}
                  onChange={(e) => setFormData({ ...formData, month: parseInt(e.target.value) })}
                >
                  {Array.from({ length: 12 }, (_, i) => i + 1).map((month) => (
                    <option key={month} value={month}>
                      {new Date(2000, month - 1).toLocaleString('vi-VN', { month: 'long' })}
                    </option>
                  ))}
                </Select>
              </FormControl>
              <FormControl isRequired>
                <FormLabel>S Tin</FormLabel>
                <Input
                  type="number"
                  value={formData.price}
                  onChange={(e) => setFormData({ ...formData, price: e.target.value })}
                  placeholder="Nhp s tin"
                  step="1000"
                  min="0"
                />
              </FormControl>
              <FormControl isRequired>
                <FormLabel>Danh Mc</FormLabel>
                <Select
                  value={formData.category}
                  onChange={(e) => setFormData({ ...formData, category: e.target.value })}
                >
                  {CATEGORIES.map((category) => (
                    <option key={category} value={category}>
                      {category}
                    </option>
                  ))}
                </Select>
              </FormControl>
              <FormControl>
                <FormLabel>Ghi Ch</FormLabel>
                <Input
                  value={formData.note}
                  onChange={(e) => setFormData({ ...formData, note: e.target.value })}
                  placeholder="Nhp ghi ch (ty chn)"
                />
              </FormControl>
            </VStack>
          </ModalBody>
          <ModalFooter>
            <Button variant="ghost" mr={3} onClick={onClose}>
              Hy
            </Button>
            <Button
              colorScheme="blue"
              type="submit"
              isLoading={isSubmitting}
            >
              {transaction ? 'Cp Nht' : 'To Mi'}
            </Button>
          </ModalFooter>
        </form>
      </ModalContent>
    </Modal>
  );
} 